.main_header {
	-webkit-text-fill-color:transparent;
  -webkit-background-clip: text;
  background-clip: text;
  background-image: url('https://images.unsplash.com/photo-1562321108-882a3b887636?ixlib=rb-1.2.1&ixid=eyJhcHBfaWQiOjEyMDd9&auto=format&fit=crop&w=1352&q=80');
	font-size: 20vw;
	background-size: cover;
  width: 100%;
	height: 100%;
  text-align: center;
	line-height: auto;
	
	padding: 0;
	margin: 0;


}
	@media screen and (-webkit-device-pixel-ratio) {
		.main_header { background-image: url('https://images.unsplash.com/photo-1562321108-882a3b887636?ixlib=rb-1.2.1&ixid=eyJhcHBfaWQiOjEyMDd9&auto=format&fit=crop&w=1352&q=80'); }
	}
.main_header span {
	font-size: 10%;
	line-height: 0px;
}